import discountTitleIds from "./../assets/discount-title.json";
import Book from "@/models/Book";

const discountSteps: number[] = [0, 0, 0.1, 0.11, 0.12, 0.13, 0.14, 0.15];

const getApplicableDiscountStep = (totalUniqueBook: number): number => {
  return totalUniqueBook >= discountSteps.length
    ? discountSteps[discountSteps.length - 1]
    : discountSteps[totalUniqueBook];
};

const DiscountCalculatorService = {
  getTotalDiscount(books: Book[]): number {
    // Find all discountable books
    const discountableBooks = books.filter(book => {
      return (discountTitleIds as string[]).includes(book.id);
    });

    const uniqueDiscountableBooks = discountableBooks.reduce((accumulator, currentBook) => {
      // Check if book is already in the unique list
      const isInList = accumulator.some(value => {
        return value.id === currentBook.id;
      });

      if (!isInList) {
        accumulator.push(currentBook);
      }

      return accumulator;
    }, [] as Book[]);

    const applicableDiscountStep = getApplicableDiscountStep(uniqueDiscountableBooks.length);

    const totalDiscountableAmount = uniqueDiscountableBooks.reduce(
      (total, currentBook) => (total += currentBook.price),
      0
    );

    const totalDiscount = totalDiscountableAmount * applicableDiscountStep;

    // console.log(
    //   uniqueDiscountableBooks.length,
    //   applicableDiscountStep,
    //   totalDiscountableAmount,
    //   totalDiscount
    // );

    return totalDiscount;
  }
};

export default DiscountCalculatorService;
